<template>
  <div class="container">
  <h1>tareas</h1>
  <b-alert
      :show="dismissCountDown"
      dismissible
      :variant="mensaje.color"
      @dismissed="dismissCountDown=0"
      @dismiss-count-down="countDownChanged"
    >
    {{ mensaje.texto }} 
  </b-alert>

  <form @submit.prevent="editarTarea(tareaEditar)" v-if="editar">
    <h3>Editar nueva tarea</h3>
    <input type="text" class="form-control my-2" placeholder="Tarea" v-model="tareaEditar.nombre">
    <input type="text" class="form-control my-2" placeholder="descripción" v-model="tareaEditar.descripcion">
    <b-button class="btn-warning my-2 btn-block" type="@submit"> Editar </b-button>
    <b-button class="my-2 " type="@submit" @click="editar = false"> Cancelar </b-button>
  </form>

  <form @submit.prevent="agregaTarea()" v-if="!editar">
    <h3>Agregar nueva tarea</h3>
    <input type="text" class="form-control my-2" placeholder="Tarea" v-model="tarea.nombre">
    <input type="text" class="form-control my-2" placeholder="descripción" v-model="tarea.descripcion">
    <b-button class="btn-success my-2 btn-block" type="@submit"> Agregar </b-button>
  </form>

  <table class="table">
  <thead>
    <tr>
      <th scope="col">id</th>
      <th scope="col">Tarea</th>
      <th scope="col">Descripcion</th>
      <th scope="col">Acciones</th>
    </tr>
  </thead>
  <tbody>
    <tr v-for="(item, index) in tareas" :key="index">
      <th scope="row">{{item._id}}</th>
      <td>{{item.nombre}}</td>
      <td>{{item.descripcion}}</td>
      <td>
        <!--<b-button @click="alerta()">Acciones</b-button>-->
        <b-button 
        @click="eliminarTarea(item._id)" 
        class="btn-danger btn-sm mx-2">Eliminar
        </b-button>
        <b-button 
        @click="activarTarea(item._id)" 
        class="btn-warning btn-sm">Editar
        </b-button>
      </td>
    </tr>
    </tbody>
  </table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tareas: [],
      mensaje: { color: '', texto: ''},
      dismissSecs: 5,
      dismissCountDown: 0,
      tarea: { nombre: "", descripcion: ""},
      editar: false,
      tareaEditar: {}
    };
  },
  created(){
    this.listarTareas();
  },
  methods: {
    alerta(){
      this.mensaje.color = 'success';
      this.mensaje.texto = 'Probando alerta';
      this.showAlert();
    },
    listarTareas(){
      this.axios.get('/tareas')
       .then(res => {
         console.log(res);
         this.tareas = res.data;
       })
       .catch(e => {
         console.log(e.response);
       })
    },
    agregaTarea(){
      //console.log(this.tarea)
      this.axios.post('/nueva-tarea', this.tarea)
        .then( res => {
          this.tareas.push(res.data)
          this.tarea.nombre = "";
          this.tarea.descripcion = "";
          this.mensaje.color = 'success';
          this.mensaje.texto = 'Tarea agregada';
          this.showAlert();
        })
        .catch( e => {
          console.log(e.response)
          this.mensaje.color = 'danger';
          this.mensaje.texto = 'PRUEBA';
          this.showAlert();
          if(e.response.data.error.errors.nombre.message){
            this.mensaje.texto = e.response.data.error.errors.nombre.message
          } else {
            this.mensaje.texto = "error del sistema"; 
            this.mensaje.color = 'danger';
          }
        })
    },
    eliminarTarea(id){
      console.log(id)
      this.axios.delete(`/tarea/${id}`)
        .then( res => {
          const index = this.tareas.findIndex(item => item._id === res.data._id);
          this.tareas.splice(index, 1);
          this.mensaje.color = 'success';
          this.mensaje.texto = 'Tarea eliminada';
          this.showAlert();
        })
        .catch(e => {
          console.log(e.response)
        })
    },
    activarTarea(id){
      this.editar = true;
      console.log(id);
      this.axios.get(`/tarea/${id}`)
       .then(res=> {
         this.tareaEditar = res.data;
       })
       .catch(e => {
          console.log(e.response)
       })
    },
    editarTarea(item){
      this.axios.put(`/tarea/${item._id }`, item)
       .then( res=> {
         const index = this.tareas.findIndex( t => t._id === res.data._id)
         this.tareas[index].nombre = res.data.nombre
         this.tareas[index].descripcion = res.data.descripcion
         this.mensaje.color = 'success';
         this.mensaje.texto = 'Tarea editada';
         this.showAlert();
         this.editar = false
       })
       .catch(e => {
          console.log(e.response)
       })
    },
    countDownChanged(dismissCountDown) {
        this.dismissCountDown = dismissCountDown
    },
    showAlert() {
      this.dismissCountDown = this.dismissSecs
    }
  }
};

</script>